.item {
    display: flex;
    border-bottom: 1px solid #E5E5E5;
    height: 48px;
    align-items: center;
}

.item:last-of-type {

}

.users {
    margin-top: 18px;
    display: flex;
    flex-direction: column;
    width: 606px;
}

.buttonsContainer {
    display: flex;
    justify-content: end;
}

.container {
    width: 630px;
}

.thead {
    background-color: #E5E5E5;
    height: 36px;
}

.thead tr {
    display: flex;
    height: 100%;
    align-items: center;
    font-weight: 600;
}

.nameCol {
    margin-left: 17px;
    grid-area: nc;
    width: 50%;
}

.ageCol {
    grid-area: ac;
    font-weight: 600;
}